body {
    font-family: Arial, Helvetica, sans-serif;
}

* {
    transition: all ease 0.2s;
    scroll-behavior: smooth;
    
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: none !important;
    }
}

.font-galyon-regular {
    font-family: 'Galyon-Regular';

    strong {
        font-family: 'Galyon-Bold';
    }
}



h1 {
    font-size: 74px;
    font-weight: bold;
    line-height: 72px;
    font-family: 'Galyon-Bold';
}

.darkblue {
    color: $darkblue;
}

.text-size-61 {
    font-size: 61px;
}

.text-size-25 {
    font-size: 25px;
}

.list-check-nice {
    border: 1.5px solid #2f55af8a;
    width: 220px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    position: relative;
    margin-top: 25px;

    p {
        padding: 20px 20px;
        font-size: 16px;
        text-align: center;
        margin: 0;
        font-family: 'Roboto-Medium';
        color: $darkblue;
    }

    &::before {
        content: '';
        position: absolute;
        width: 45px;
        height: 50px;
        background-color: $white;
        top: -15px;
        left: -15px;

    }

    &::after {
        content: '';
        position: absolute;
        background-image: url('../images/icon-check.svg');
        width: 28px;
        height: 28px;
        top: -15px;
        left: -15px;
    }
}

.fs-main-info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h1 {
        position: relative;

        &::after {
            position: absolute;
            content: '';
            background-image: url('../images/buscar.svg');
            width: 90px;
            height: 90px;
            top: -74px;
            right: 21px;
        }
    }

    p {
        font-family: 'Roboto-Regular';
        color: $black;

    }
}

