.bg-firts-banner {
    background-image: url('../images/first-banner.png');
    background-repeat: no-repeat;
    height: 760px;
}

.bg-second-banner {
    background-image: url('../images/second-banner.png');
    background-repeat: no-repeat;
    height: 760px;
}


@media (max-width: 1025px) {
    .bg-firts-banner {
        background-position: center;
    }

    .bg-second-banner {
        background-position: right;
    }

}

@media (max-width: 769px) {
    .bg-firts-banner {
        height: 690px;
    }

}

@media (max-width: 426px) {
    .bg-firts-banner {
        background-image: url('../images/first-banner-mobile.png');
    background-repeat: no-repeat;
        height: 480px;
    }

    .bg-second-banner {
        background-image: url('../images/second-banner-mobile.png');
        background-repeat: no-repeat;
        height: 100%;
        background-position: inherit;
    }

}


