.btn-sign-in {
    a {
        font-size: 20px;
        background-color: $yellow;
        padding: 15px 55px;
        color: $darkblue;
        font-weight: bold;
        border-radius: 10px;
        position: relative;
        font-family: 'Roboto-Bold';
        -webkit-box-shadow: 0px 3px 8px 2px rgba(0, 0, 0, 0.23);
        -moz-box-shadow: 0px 3px 8px 2px rgba(0, 0, 0, 0.23);
        box-shadow: 0px 3px 8px 2px rgba(0, 0, 0, 0.23);

        &::after {
            position: absolute;
            content: '';
            background-image: url('../images/icon.svg');
            right: 10px;
            width: 30px;
            height: 30px;
            transform: rotate(-90deg);
            transition: all 0.3s;

        }

        &:hover {
            color: $blue;
            background-color: $white;
        }

    }
}

.btn-sign-up {
    a {
        font-size: 20px;
        background-color: #FFFFFF;
        padding: 15px 55px;
        color: #001f5b;
        font-weight: bold;
        border-radius: 10px;
        position: relative;
        font-family: 'Roboto-Bold';

    }
}


@media (max-width: 426px) {
    .btn-sign-in {
        text-align: center;

        a {
            font-size: 18px;
            padding: 10px 45px;

            &::after {
                position: absolute;
                content: '';
                background-image: url('../images/icon.svg');
                right: 10px;
                width: 28px;
                height: 28px;
                transform: rotate(-90deg);
                top: 8px;
            }
        }
    }

}


#btn-0 {
    $btn-color-dark: shade($btn-color, 40%);
    color: tint($btn-color);

    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: $btn-color-dark;
        transform: translateX(-100%);
    }

    &:hover {
        color: tint($btn-color, 75%);

        &:before {
            transform: translateX(0);
        }
    }
}


